import ActionMobile from '~/components/Exchange/ExchangeMobile/ActionMobile'

export default {
  props: {
    UserBalance: {
      require: true,
      type: Object,
    },
  },
  components: {
    ActionMobile,
  },
  data() {
    return {
      containerState: false,
      drawerType: 'BUY',
      showTest: false,
    }
  },
  computed: {
    isLoggedIn() {
      return !!this.$cookies.get('Token')
    },
  },
  methods: {
    showDrawer(type, state) {
      this.drawerType = type
      this.containerState = state
      this.showTest = state
      // alert(type)
    },
  },
}
