  import formatter from "~/Mixins/formatter"

export default {
  mixins: [formatter],

  props: {
    list: {
      default: () => [],
      type: Array,
    },
    valueColClass: {
      type: String,
      default: '',
    },
    limit: {
      type: Number,
      default: 9,
    },
    styles: {
      type: Object,
      required: false,
    },
    showTotal: {
      type: Boolean,
      required: false,
      defaut: false
    }
  },
  methods: {
    updateActionPrice(v) {
      this.$store.commit('transactions/updateActionPrice', v)
    },
  },
  computed: {
    listLimit() {
      const { list } = this
      if (list.length === 0) return
      return new Array(this.limit).fill(0).map((v, i) => {
        return list[i]
      })
    },
  },
}
