import { mapGetters } from 'vuex'
import formatter from '~/Mixins/formatter'
export default {
  mixins: [formatter],
  props: {
    type: {
      type: String,
      default: 'asks',
    },
    list: {
      default: () => [],
      type: Array,
    },
    tab:{
      type: Number
    },
    valueColClass: {
      type: String,
      default: '',
    },
    limit: {
      type: Number,
      default: 9,
    },
    styles: {
      type: Object,
      required: false,
    },
    showTableHeaders: {
      required: false,
      default: true,
    },
    scrollPos: {
      type: String,
      default: 'top',
    },
  },
  methods: {
    updateActionItems(v) {
      this.$store.commit('transactions/updateActionPrice', v[0])
      if (this.type === 'asks') {
        this.$store.commit('transactions/updateActionBuyTotal', v[0] * v[1])
      }
      if (this.type === 'bids') {
        this.$store.commit('transactions/updateActionSellQuantity', v[1])
      }
    },
  },
  computed: {
    ...mapGetters({
      getSymbolData: 'symbol/getSymbolData',
    }),
    listLimit() {
      const { list } = this 
      if (list.length === 0) return
      return new Array(this.limit).fill(0).map((v, i) => {
        return list[i]
      })
    },
  },
  watch: {
    list: {
      handler() {
        if (this.scrollPos === 'bottom') {
          const container = this.$el.querySelector('#dataTable')
          container.scrollTop = container.scrollHeight
        }
      },
      deep: true,
    },
  },
  mounted() {
    if (process.client && this.scrollPos === 'bottom') {
      const container = this.$el.querySelector('#dataTable')
      container.scrollTop = container.scrollHeight
    }
  },
}
