import TransacitonTypeCard from './TransacitonTypeCard'
import ValueCard from './ValueCard'
import Transactions from './Transactions'

export default {
  components: {
    TransacitonTypeCard,
    ValueCard,
    Transactions,
  },
  props: {
    transactions: {
      type: Object,
      default: () => {},
    },
    lastPrice: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      type: 1,
    }
  },
  computed: {
    getBids() {
      return this.transactions.bids
    },
    getAsks() {
      return this.transactions.asks
    },
  },
  methods: {
    isActiveTab(v) {
      return this.type === v
    },
  },
}
