import formatter from '~/Mixins/formatter'
export default {
  mixins: [formatter],
  props: {
    openOrdersData: {
      required: true,
      type: Array,
    },
    last24HoursData: {
      required: true,
      type: Array,
    },
    isOpenOrdersLoading: {
      required: false,
      type: Boolean,
    },
  },
  data() {
    return {
      mobileOrderTabs: [
        { key: 'openOrders', scopedSlots: { tab: 'titleSlot' } },
        { key: 'last24HOrders', scopedSlots: { tab: 'titleSlot' } },
      ],
      activeMobileOrderTab: 'openOrders',
    }
  },
}
